.App {
  justify-content: space-between;
  margin: 4% auto;
  width: 50%;
  min-width: 1024px;
  .nft_preview{
    flex-direction: column;
    width: 36%;
    .nft_name{
      margin-bottom: 30px;
      align-items: center;
      height: 30px;
      z-index: 9999;
      img{
        width: 28px;
        margin-right: 6px;
        cursor: pointer;
      }
      input{
        background: #D9D9D9;
        border: none;
        padding: 5px;

        font-family: 'Inter', sans-serif;
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 15px;
        width: 100px;        
      }
    }
    .nft_generated{
      svg{
        width: auto;
        height: 340px;
        margin: -80px -40px 0px;
      }
      div{
        flex-direction: column;
        justify-content: space-between;
        margin-top: 10px;
        margin-left: 10px;
        img{
          width: 72px;
          cursor: pointer;
        }
      }
    }
    .favorites{
      flex-direction: column;
      div{
        svg{
          width: auto;
          height: 340px;
          margin: -80px -20px 0px;
        }
        .favorites_side_menu{
          flex-direction: column;
          justify-content: center;
          align-items: flex-end;
          button{
            width: 20px;
            border: none;
            margin-bottom: 10px;
          }
          img{
            width: 72px;
            cursor: pointer;
          }
        }
      }
    }
  }
  .generator_menu{
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    width: 50%;
    span{
      margin-bottom: 10px;
      .points{
        flex-wrap: wrap;
        width: 140px;
        button{
          border: none;
          background: none;
          margin: 0;
          width: 32.1px;
          height: 32.1px;
          img{
            transition: all 0.4s;
            width: 26px;
            cursor: pointer;
          }
        }
      }
      .direction{
        flex-direction: column;
        justify-content: space-between;
        width: 80%;
        header{
          display: flex;
          align-items: center;
          justify-content: space-between;
          label{
            display: flex;
            align-items: center;
            img{
              width: 26px;
              margin-right: 6px;
              cursor: pointer;
            }
          }
          button{
            border: none;
          }
        }
        .dropbtn {
          margin-top: 0px;
          background: #D9D9D9;
          border: none;
          padding: 5px;
          width: 200px; 

          font-family: 'Inter', sans-serif;
          font-style: normal;
          font-weight: 400;
          font-size: 16px;
          line-height: 15px;
          margin-bottom: 4px;
          text-align: left;
          padding-left: 12px;
        }
        
        .dropbtn:hover, .dropbtn:focus {
          background-color: #f1f1f1;
          transition: all 0.4s;
        }
        
        .dropdown {
          position: relative;
          display: inline-block;
        }
        
        .dropdown-content {
          display: none;
          position: absolute;
          max-height: 500px;
          background-color: #f1f1f1;
          min-width: 200px;
          overflow: auto;
          box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
          z-index: 1;
        }
        
        .dropdown-content div {
          color: black;
          padding: 6px 8px;
          text-decoration: none;
          display: block;
        }      
        .dropdown div:hover {background-color: #ddd;}

        .show {display: block;}  
      }
    }
    .plus_trait{
      margin-top: 5px;
      width: 110px;
      justify-content: flex-end;
      img{
        cursor: pointer;
        width: 60px;
      }
    }
  }
}

div, span{
  display: flex;
}

